.navbar {
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  padding: 24px 104px;
  align-items: center;
}

.navbar__list {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar__item {
  margin-right: 32px;
}

.navbar__link {
  position: relative;
  display: block;
  padding: 8px 0;
  text-decoration: none;
  color: #2D3648;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.01em;
  text-align: center;
}

.navbar__link::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #2D3648;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
  transform: scaleX(0);
}

.navbar__link:hover::after {
  transform: scaleX(1);
}
