.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 640px;
  margin: 0 auto;
}

.heading {
  color: #2D3648;
  text-align: center;
  font-family: Inter, sans-serif;
  font-size: 56px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: 1px;
  margin: 0;
  padding: 0;
}

.button-container {
  display: flex;
  justify-content: space-around;
}
