.button {
  border: none;
  height: 48px;
  border-radius: 4px;
  text-align: center;
  margin: 0 auto;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.01em;
  padding: 0 12px;
}

.button:hover {
  cursor: pointer;
}

.button-primary {
  color: #FFFFFF;
  background-color: #2D3648;
}

.button-secondary {
  color: #2D3648;
  background-color: #FFFFFF;
}
